/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&family=Roboto:ital,wght@0,400;0,500;1,900&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1.5;
  font-family: "Roboto", "Noto Sans TC", -apple-system, BlinkMacSystemFont, "Microsoft JhengHei", "Segoe UI",  "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-decoration: none;
  display: block; }

.container {
  max-width: 1140px;
  padding: 0 15px;
  margin: 0 auto;
  width: 100%; }

.row-reverse {
  flex-direction: row-reverse; }

.m-0 {
  margin: 0; }

.header {
  background: #AA0601;
  display: flex;
  justify-content: space-between;
  padding: 30px 88px; }
  @media (max-width: 767px) {
    .header {
      padding: 0;
      flex-direction: column;
      flex-wrap: wrap;
      max-width: 100%; } }
  .header ul {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
    .header ul .nav-item {
      margin-left: 64px; }
      @media (max-width: 768px) {
        .header ul .nav-item {
          margin-left: 40px; } }
      @media (max-width: 767px) {
        .header ul .nav-item {
          width: 50%;
          margin: 0;
          border: 1px solid #fff;
          text-align: center; } }
    .header ul .nav-link {
      color: #fff;
      font-size: 1.25rem;
      padding: 10px; }
  .header .logo {
    width: 80px;
    height: 49px; }
    @media (max-width: 767px) {
      .header .logo {
        align-self: center;
        width: 48px;
        height: 29px;
        margin: 10px 0; } }

.footer {
  background: #AA0601;
  padding: 32px 0; }
  .footer .container {
    color: #fff; }
    .footer .container a {
      color: #fff; }
  .footer .nav {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 24px; }
  .footer .nav-list {
    display: flex;
    margin-bottom: 24px; }
    .footer .nav-list li {
      margin-right: 24px; }
    .footer .nav-list a {
      padding: 12px 24px 12px 0; }
    @media (max-width: 767px) {
      .footer .nav-list {
        display: none; } }
  .footer .contact-list {
    font-size: 2.25rem; }
    .footer .contact-list .phone {
      margin-right: 20px;
      width: 36px;
      height: 36px; }
    .footer .contact-list .mail {
      margin-right: 20px;
      width: 40px;
      height: 32px; }
    @media (max-width: 767px) {
      .footer .contact-list {
        font-size: 1rem; }
        .footer .contact-list .phone {
          margin-right: 6.5px;
          width: 15px;
          height: 15px; }
        .footer .contact-list .mail {
          margin-right: 6.5px;
