          height: 13px; } }
  .footer .social-list {
    display: flex; }
    .footer .social-list li {
      margin-right: 32px; }
    @media (max-width: 767px) {
      .footer .social-list img {
        width: 24px;
        height: 24px; } }
  .footer .copyright {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    border-top: 1px solid #fff;
    padding-top: 32px; }
    .footer .copyright ul {
      display: flex; }
    .footer .copyright li {
      margin-left: 24px; }
    .footer .copyright a {
      padding: 12px 0 12px 24px;
      font-size: 14px; }
    @media (max-width: 767px) {
      .footer .copyright p {
        width: 100%;
        font-size: 14px; }
      .footer .copyright ul {
        flex-direction: column;
        align-items: flex-start; }
      .footer .copyright li {
        margin: 0; }
      .footer .copyright a {
        padding: 12px 24px 12px 0;
        font-size: 14px; } }

